<template>
  <div>
    <v-select chip :items="genres" item-title="genreName" item-value="genre" label="selected Genres" multiple v-model="selectedGenres">
      <template #selection="{item}">
        <p>{{ item.genre }}, &nbsp;</p>
      </template>
    </v-select>
    <v-text-field hide-details outlined class="mb-4" :loading="loading" label="Video Number" type="number" v-model="videoNumber"></v-text-field>
    <v-text-field hide-details outlined class="mb-4" :loading="loading" label="Video URL" type="text" v-model="videoURL"></v-text-field>
    <v-text-field hide-details outlined class="mb-4" :loading="loading" label="Song Name" type="text" v-model="songName"></v-text-field>
    <v-text-field hide-details outlined class="mb-4" :loading="loading" label="Artist Name" type="text" v-model="artistName"></v-text-field>
    <v-text-field hide-details outlined class="mb-4" :loading="loading" label="Album Name" type="text" v-model="albumName"></v-text-field>
    <v-switch hide-details outlined class="mb-4" :loading="loading" label="Yes OR No" type="boolean" v-model="yesOrNo"></v-switch>
    <v-text-field hide-details outlined class="mb-4" :loading="loading" label="Video Length" type="text" v-model="videoLength"></v-text-field>
    <v-text-field hide-details outlined class="mb-4" :loading="loading" label="Video Length in Seconds" type="number" v-model="videoLengthInSeconds"></v-text-field>
    <v-text-field hide-details outlined class="mb-4" :loading="loading" label="Main Genre" type="text" v-model="mainGenre"></v-text-field>
    <v-text-field hide-details outlined class="mb-4" :loading="loading" label="Sub Genre" type="text" v-model="subGenre"></v-text-field>
    <v-text-field hide-details outlined class="mb-4" :loading="loading" label="Spotify ID" type="text" v-model="spotifyId"></v-text-field>
    <v-btn @click="submit" block color="primary" size="x-large">Submit</v-btn>
  </div>
</template>
<script>
  import { mapActions } from 'vuex'
  import axios from 'axios';
  export default {
    data(){
      return {
        videoNumber: 0,
        videoURL: '',
        songName: '',
        artistName: '',
        albumName: '',
        yesOrNo: false,
        videoLength: '3:15',
        videoLengthInSeconds: 0,
        mainGenre: '',
        subGenre: '',
        otherGenres: '',
        loading: false,
        selectedGenres: [],
        genres: [],
        spotifyId: '',
      }
    },
    methods: {
      ...mapActions({
      showToast: "ui/showToast"
    }),
      async submit(){
        if(this.loading) return;
        try {
          this.loading = true;
          console.log({songName: this.$data })
          const response = await axios.post(`https://timikeys.up.railway.app/api/v1/performances/add`, {...this.$data, videoNumber: Number(this.videoNumber), videoLengthInSeconds: Number(this.videoLengthInSeconds)});
          // const response = await res.json()
          console.log({response});
          if(response.data.success){
            this.showToast({ show: true, message: response.data.message, sclass:"success", timeout: 3000})
          } else {
            this.showToast({ show: true, message: response.data.message, sclass:"error", timeout: 3000})
          }
        } catch (error) {
          console.log(error);
          this.showToast({ show: true, message: error.message, sclass:"error", timeout: 3000})
        } finally {
          this.loading = false;
        }
      },
      async getGenres(){
        if(this.loading) return;
        try {
          const res = await fetch('https://timikeys.up.railway.app/api/v1/performances/genres')
          const {data} = await res.json();
          console.log({data})
          this.genres = data;
        } catch (error) {
          console.log({error});
        }
      }
    },
    async mounted(){
      await this.getGenres()
    }
  }
</script>
